  },
  methods: {

    //   
    // example_limit_1 - ,   
    //  example_limit_2 - ,   
    getColor(data, example_limit_1, example_limit_2){
      if (data <= example_limit_1) 
      {
        return 'green'
      } 
      else if (data <= example_limit_2) 
      {
        return 'orange'
      }
      return 'red'
    },

    //   ,          
    getColorBinary( violation ){
      if (violation) return 'red'
      return 'grey'
    },

// //              (   1 )
//     W: 0,     //   , 
//     windField: 1, //   
//     W_0: 400,   //      10   
//     v_0: 25,    //  
    windWriteSelected(data){
      console.log('wind data', `${JSON.stringify(data[0])}`)
      //        ,   
      this.W = data[0].Norma * 1000     //   , 
      this.windField = data[0].airZone
      this.W_0 = data[0].windPressure
      this.v_0 = data[0].windSpeed
      //  
      this.chooseZoneWind = false
    },


//             (   1 )
//    C: 10,       //    ()
//    iceField: 1,  //   
    iceWriteSelected(data){
      console.log('ice data', `${JSON.stringify(data[0])}`)
        //        ,   
        this.C = data[0].iceWidth     //   , 
        this.iceField = data[0].Icearea
        //  
        this.chooseZoneIce = false
    },
    
    cableWriteSelected(data){
      this.chosenCable = data[0]
      console.log('Final get data', this.chosenCable)
      this.chooseCable = false
      this.setCableParams(this.chosenCable)
    },

    setCableParams(chosenCable){
      this.E_kab = chosenCable.L_nach    //    (/^2)
      //   ,   .  ,  ,  ...  
      this.E_vit = chosenCable.L_feat
      this.E_kon = chosenCable.L_kon
      this.S_kab=chosenCable.Slice
      this.Diameter = chosenCable.Diameter
      this.TKLR = chosenCable.TKLR * 0.000001
      this.m = chosenCable.Weight
    },

    DotToCommas(data){
      return data.toString().replace(".",",")
    },

    /**
     *  , 
     * @param {number} value           -   X,    
     * @param {massive} dataTable -  ,  .   
     * @param {string} searchableValue -    
     * @param {string} SearchThrough   -     X
     */
    interpolateUniversal(value, dataTable, searchableValue, SearchThrough){
      //   
      var minValueX = dataTable[0][SearchThrough]
        var minValueSearched = dataTable[0][searchableValue]  

      //   
      var maxValueX = dataTable[
        dataTable.length - 1
      ][SearchThrough]
        var maxValueSearched = dataTable[
          dataTable.length - 1
        ][searchableValue] 

      if(this.debug){
        console.log(`  ${SearchThrough} `, minValueX)
        console.log(`  ${searchableValue} `, minValueSearched)

        console.log(`  ${SearchThrough} `, maxValueX)
        console.log(`  ${searchableValue} `, maxValueSearched)
        
        console.log(' ', value)
      }
      

      //  
      if (value <= minValueX){
        if(this.debug) console.warn(`
          ${searchableValue}  ${SearchThrough}=${value},
          , ${value}, '<', ${minValueX}
        `
        )
        if(this.debug) console.log(':', minValueSearched)
        return parseFloat(minValueSearched)
      }
      //  
      if (value >= maxValueX){
        if(this.debug) console.warn(`
          ${searchableValue}  ${SearchThrough}=${value},
          , ${value}, '>', ${maxValueX}
        `
        )
        if(this.debug) console.log(':', maxValueSearched)
        return parseFloat(maxValueSearched)
      }

      //     
      if(this.debug){
        console.log(` ,   ${value},    ${dataTable[0][SearchThrough]},   = ${dataTable.length}`)
      }
      for (var i = 0; i<dataTable.length; i++){
        if(this.debug){
          console.log(` ${i},  ${value},   ${dataTable[i][SearchThrough]}`)
        }

        if (value < dataTable[i][SearchThrough]){
          var x1 = dataTable[i-1][SearchThrough]
          var x2 = dataTable[i][SearchThrough]

          var y1 = dataTable[i-1][searchableValue]
          var y2 = dataTable[i][searchableValue]

          var result = this.interpolateFormula(value, x1, x2, y1, y2)
          if(this.debug) console.log( searchableValue, '=', result,'    (',value, x1, x2, y1, y2,')')
          return parseFloat(result)
        }
      }
    },

    /** 
     *  ,     . 
     *     interpolateUniversal
     * @param {number} value     ,     Y
     * @param {number} x1          
     * @param {number} x2          
     * @param {number} y1        Y  
     * @param {number} y2        Y  
     */
    interpolateFormula(value, x1, x2, y1, y2){
      return (y1 + (value-x1)* ((y2-y1)/(x2-x1))).toFixed(this.decimalsRounding+1)
    },

    start() {
      this.started = true
      //    ... ...
      //     ,   .
      //     ,      
      if(this.L < 1){
        this.L = 1
      }
      this.task_2_1()
      this.task_2_2()
      this.task_2_3()
      this.task_2_4()
      this.task_2_5()
      this.task_2_6()
      this.task_2_7()
      this.task_2_8()
      this.task_2_9()
      this.task_2_10()
      this.task_2_11()
      this.task_2_13()
    },
    
    //  
    task_2_1(){
      this.W_kab = parseFloat(
        this.m * this.g / 1000
        ).toFixed(this.decimalsRounding)
      console.log('2.1  , /', this.W_kab)
    },

    //  ,   
    task_2_2(){

      this.H_nach = ((this.W_kab * this.L^2) / ( 8 * this.S )).toFixed(this.decimalsRounding+2)
      console.log('2.2  , ', this.H_nach)
    },

    //    
    task_2_3(){
      this.L1 = (
        this.L - (2 * this.h * this.H_nach)/(this.W_kab * this.L)
      ).toFixed(this.decimalsRounding+2)
      console.log('2.3   ', this.L1)

      this.L2 = (
        this.L + (2 * this.h * this.H_nach)/(this.W_kab * this.L)
      ).toFixed(this.decimalsRounding+2)
      console.log('2.3   ', this.L2)

      // this.S1 = (
      //   (this.W_kab * this.L1^2)/(8 * this.H_nach)
      // ).toFixed(this.decimalsRounding+2)
      this.S1 = this.get_S1_or_S2(this.S, this.L1, this.L)
      console.log('2.3   ', this.S1)

      // this.S2 = (
      //   (this.W_kab * this.L2^2)/(8 * this.H_nach)
      // ).toFixed(this.decimalsRounding+2)
      this.S2 = this.get_S1_or_S2(this.S, this.L2, this.L)
      console.log('2.3   ', this.S2)
    },

    //   
    task_2_4(){
      this.L_kab = (
        this.L + 4/3 * ( (this.S1^2 / this.L1) + (this.S2^2 / this.L2) )
      ).toFixed(this.decimalsRounding)
      console.log('2.4   , ', this.L_kab)
    },

    //     
    task_2_5(){
      this.L_n0 = (
        this.L_kab / (1 + (this.H_nach / (this.E_kab * this.S_kab)))
      ).toFixed(this.decimalsRounding)
      console.log('2.5     , ', this.L_n0)
    },

    //        
    task_2_6(){
      this.L_nk = parseFloat(
        this.L_n0 * (1 + this.TKLR * (this.T - this.T_sr))
      ).toFixed(this.decimalsRounding)
      // console.log('2.6 ', this.TKLR)
      // console.log('2.6 ', 1 + this.TKLR * (this.T - this.T_sr))
      console.log('2.6        , ', this.L_nk)
    },

    //     
    //      
    task_2_7(){
      //    Ki, Kd  d
      this.height-=this.S
      // this.interpolateHeight(this.height)
      this.K_i = this.interpolateUniversal(
          this.height, 
          this.heightTable, 
          'K_i', 
          'height'
        )
      this.K_d = this.interpolateUniversal(
          this.height, 
          this.heightTable, 
          'K_d', 
          'height'
        )
      this.d = this.interpolateUniversal(
          this.height, 
          this.heightTable, 
          'd', 
          'height'
        )
      // this.interpolateUniversal(this.height, this.heightTable, 'K_i', 'height')

      this.W_g = parseFloat(
        parseFloat(this.W_kab) + 
        parseFloat(this.Ro_L * this.g * Math.PI * this.K_i * this.K_d * this.C * (this.d + this.C))
        ).toFixed(this.decimalsRounding)
      // console.log('2.7 ', parseFloat(this.W_kab))
      // console.log('2.7 ', this.Ro_L)
      // console.log('2.7 ', this.g)
      // console.log('2.7 ', Math.PI)
      // console.log('2.7 ', this.K_i)
      // console.log('2.7 ', this.K_d)
      // console.log('2.7 ', this.C)
      // console.log('2.7 ', this.d)
      // console.log('2.7 ', parseFloat(this.d + this.C))
      // console.log('2.7 ', parseFloat(this.Ro_L * this.g * Math.PI * this.K_i * this.K_d * this.C * (this.d + this.C)).toFixed(this.decimalsRounding))
      
      console.log('2.7      , ', this.W_g)
      this.height+=this.S
    },

    //      
    task_2_8(){
      console.log("2.8      .      W_0")
      //     
      this.K_l = this.interpolateUniversal(
          this.height, 
          this.K_lTable, 
          'K_l', 
          'height'
        )
      //     
      this.K_w = this.interpolateUniversal(
        this.height, 
        this.K_wTable, 
        `K_w_${this.terrainType}`, 
        'height'
      )

      // a_w -    
      this.a_w = this.interpolateUniversal(
        this.W_0, 
        this.a_wTable, 
        `a_w`, 
        'W_0'
      )

      // C_x -     
      if(this.d >= 20){
        this.C_x = 1.1
      } else { this.C_x = 1.2}

      if(this.debug){
        console.log('Diameter =', this.d)
        console.log('C_x =', this.C_x)
      }
      

      /*W -     

              -   (W_0)
              -   
                         v_

                    W = v^2 / 1.6

                     v_ , 
                    W_ = 0.25 * W_0
      */      
      this.W_v_wind = parseFloat(this.windPressure(this.W_0))
      console.log('  :', this.W_v_wind)

      if(this.debug) console.log('  ,  ,  W_ = 0.25*W_0 =', this.W_0/4)
      this.W_v_ice = parseFloat(this.windPressure(this.W_0/4))
      console.log('  :', this.W_v_ice)
    },

    //  ,   
    task_2_9(){
      this.W_max = parseFloat(
        Math.sqrt(
          (this.W_v_wind*this.W_v_wind) + (this.W_v_ice*this.W_v_ice)
        ).toFixed(this.decimalsRounding)
      )
      console.warn('2.9  ,    ???', this.W_max)
    },

    //    
    task_2_10(){
      
      //     
      this.L_max = this.L + (4/3)*((this.S1^2/this.L1)+(this.S2^2/this.L2))

      //  S1  S2
      console.log(' S1  S2')
      console.log(`:    S1=${this.S1}, S2=${this.S2}`)

      this.S1 = parseFloat(
        this.S*this.L1*this.L1/(this.L*this.L)
      ).toFixed(this.decimalsRounding)

      this.S2 = parseFloat(
        this.S*((this.L2)^2)/((this.L)^2)
      ).toFixed(this.decimalsRounding)

      console.log(`: S1=${this.S1}, S2=${this.S2}`)
      var a = this.makeA(this.L, this.h, this.L_nk)
      a = parseFloat(a.toFixed(this.decimalsRounding+2))
      var b = this.makeB(this.W_max, this.L, this.L_nk, this.E_kab, this.S_kab)

      b = parseFloat(b.toFixed(this.decimalsRounding+2))
      // console.log('a =',a, 'and b =',b)

      this.S_max = this.kubicEquasion(a,b)
      this.S_max = parseFloat(this.S_max.toFixed(this.decimalsRounding+2))
      console.log('2.10    =',this.S_max)
      console.log('2.10      =',this.S)
    },

    //      
    task_2_11(){
      this.H_max = (this.W_max * (this.L^2)) / (8 * this.S_max)
      this.H_max = parseFloat(this.H_max.toFixed(this.decimalsRounding+2))
      console.log('2.11      ', this.H_max, '')
      console.log(' ,     2.2', this.H_nach, '')
    },

    //         
    task_2_13(){
      console.log('2.13         ')
      //   
      var a = this.makeA(this.L, this.height, this.L_nk)
      var b = this.makeB(this.W_kab, this.L, this.L_nk, this.E_vit, this.S_kab)
      this.S_n_vit = this.kubicEquasion(a, b).toFixed(this.decimalsRounding)
      console.log('  ', this.S_n_vit)
      //    (   )
      this.H_n_vit = (
          (this.W_kab * (this.L^2)) / (8 * this.S_n_vit)
        ).toFixed(this.decimalsRounding)
      console.log('  ', this.H_n_vit)
    },

    /**
     *   S,    
     * @param {number} S      -
     * @param {number} L1     -
     * @param {number} L_big  -
     * 
     * @return {number} S_n  -    S,    2.4
     */
    get_S1_or_S2(S, L1, L_big){
      return parseFloat(
        S*(L1^2)/(L_big^2)
      ).toFixed(this.decimalsRounding+2)
    },

    another_get_S1_or_S2(W, L_n, H){
      return parseFloat(
        (W*L_n*L_n) / (8*H)
      ).toFixed(this.decimalsRounding+2)
    },

    /**
     *    
     * @param L 
     * @param S1 
     * @param S2 
     * @param L1 
     * @param L2 
     */
    get_strela_provesa(L, S1, S2, L1, L2){
      return (
        L + 4/3 * ( (S1^2 / L1) + (S2^2 / L2) )
      ).toFixed(this.decimalsRounding)
    },

    /**
     *    A
     * @param {number} L     -    
     * @param {number} h     -  
     * @param {number} L_nk  -    
     */
    makeA(L, h, L_nk){
      var result = 3*((L^2) + (((h)^2)/2) - L*L_nk)/8
      if(this.debug) console.log('a from makeA',result)
      return result
    },   

    /**
     *    B
     * @param {number} W     -   
     * @param {number} L     -    
     * @param {number} L_nk  -    
     * @param {number} E     -  
     * @param {number} S     -  
     */
    makeB(W, L, L_nk, E, S){
      if(this.debug) {
        console.log('W =', W)
        console.log('L =', L)
        console.log('L_nk =', L_nk)
        console.log('E =', E)
        console.log('S =', S)
      }
      var result = parseFloat(
        (-3*W*((L)^3)*L_nk)/(64*E*S)
      )
      if(this.debug) console.log('b from makeB',result)
      return result
    },

    /**
     *      S_max
     *   2.10
     * @param {number} a -  
     * @param {number} b -  
     */
    kubicEquasion(a, b){
      
      var parametr = (a/3)^3 + (-b/2)^2
      console.log('parametr',parametr)
      var answer = 0
      if(parametr>=0){
        answer = (
          (
            (-b/2) + Math.sqrt(parametr)
          )^(1/3) + (
            (-b/2) - Math.sqrt(parametr)
          )^(1/3)
        )
        if(this.debug) console.log(answer, ' 1')
      }
      else{
        answer =  (
          2 * Math.sqrt(-a/3)*Math.cos(
            (1/3)*Math.cos(
              (-b/2)/(
                (-a/3)^(3/2)
              )
            )^(-1)
          )
        )
        if(this.debug) console.log(answer, ' 2')
      }
      return answer 
    },

    /**
     *         2.8
     *       
     * @param W -   , ,   :  . ,  . 
     */
    windPressure(W){
      if (this.debug){
        console.log('a_w =', this.a_w)
        console.log('K_l =', this.K_l)
        console.log('K_w =', this.K_w)
        console.log('C_x =', this.C_x)
        console.log('d =', this.d)
        console.log('C =', this.C)
      }
      
      
      return parseFloat(
        this.a_w * this.K_l * this.K_w * this.C_x * W * parseFloat(this.d + 2* this.K_i * this.K_d * this.C ) * 0.001  
      ).toFixed(this.decimalsRounding)
    },


    //    ,    
    test(){
      console.log("lets test")
      console.log(this.W)
    },
  },
  computed: {
    
    
    //   
    rulesViolation () {
      return (
        // 2.1
        this.violation_2_1
        // 2.2
        || this.violation_2_2
        // 2.3
        || this.violation_2_3
        // 2.4
        || this.violation_2_4
        // 2.5
        || this.violation_2_5
        // 2.6
        || this.violation_2_6
        // 2.7
        || this.violation_2_7
      )
    },

    violation_2_1 () {
      return (this.W_kab<=0 && !! this.W_kab )
    },
    violation_2_2 () {
      return (this.H_nach<=0 && !!this.H_nach )
    },
    violation_2_3 () {
      return ((this.L1>this.L2) || (this.L2<this.L1) || (this.S1<=0) || (this.S2<=0 && this.S2>this.height))
    },
    violation_2_4 () {
      return (this.L_kab<this.L)
    },
    violation_2_5 () {
      return (this.L_n0<this.L)
    },
    violation_2_6 () {
      return (this.L_nk<this.L)
    },
    violation_2_7 () {
      return (this.W_g>50)
    },
