<template>
  <div>
    <!--  -->
    <div class="header">
        <headerTab 
            title="      "
            v-bind:info = "componentInfo"
        ></headerTab>
    </div>
    
    <!--   -->
    <v-card class="pa-5" tile>
      

      
        <v-text-field
          type="number"
          outlined
          required
          clearable
          label="   ()"
          :rules="rule"
          hide-details="auto"
          v-model.number="voultage"
          class="mt-5"
        >
            <template v-slot:label>
            <toolbarInfo
                title="   ()"
                desc = "         ,   ."
            />
            </template> 
        
        </v-text-field>
      
        <v-text-field
          type="number"
          outlined
          required
          clearable
          label="   (/)"
          :rules="rule"
          hide-details="auto"
          v-model.number="capacity"
          class="mt-5"
        >
            <template v-slot:label>
            <toolbarInfo
                title="   (/)"
                desc = " ,        ,     ."
            />
            </template> 

        </v-text-field>

        <v-text-field
          type="number"
          outlined
          clearable
          required
          label="-    (.)"
          :rules="rule"
          hide-details="auto"
          v-model.number="batteries"
          class="mt-5"
          >
            <template v-slot:label>
            <toolbarInfo
                title="    (.)"
                desc = "    .        .       ().   ."
            />
            </template> 

        </v-text-field>

        <v-text-field
          type="number"
          outlined
          required
          clearable
          label="  (0,8 - 1,0)"
          :rules="rule"
          hide-details="auto"
          v-model.number="kpd"
          class="mt-5"
        >
            <template v-slot:label>
            <toolbarInfo
                title="    (0,8 - 1,0)"
                desc = " ,         ,  0,8 - 80%,  1,0 - 100%."
            />
            </template> 

        </v-text-field>

        <v-row class="mx-0 my-2">

        
            <v-text-field
                type="number"
                :disabled="self === false "
                outlined
                clearable
                label=".    ()"
                :rules="rule"
                hide-details="auto"
                v-model.number="power"
                class="mt-5"
            >
                <template v-slot:label>
                    <toolbarInfo
                        title=".    ()"
                        desc = "   .   .         ,      .     :   = 700   ,   = 1000   ."
                    />
                </template> 

            </v-text-field>

            <v-checkbox 
                info
                hide-details
                label=" ?" 
                v-model="self"
                class="mt-8 ml-2"
            />

        </v-row>
      <v-btn 
          @click="start" 
          color="primary"
          class="mt-5"
          width="100%"
      ></v-btn>
  </v-card>

  <!--    -->
  <v-snackbar
          v-model="snackbar"
          :timeout="timeout"
          
      >
      {{ errorText }}

      <template v-slot:action="{ attrs }">
          <v-btn
          color="accent"
          text
          v-bind="attrs"
          @click="snackbar = false"
          >
          
          </v-btn>
      </template>
      </v-snackbar>

  <v-card 
      class="pa-5 mt-5"
      v-show="started"
  >
      <h2>  :</h2>
      <p><strong>{{ result.toFixed(3) }}</strong>, .  
      <strong>{{ (result*60).toFixed(0) }}</strong>, .</p>
      <v-btn
        @click="started=false"
        class="mt-2"
      ></v-btn>
 </v-card>

 
</div>
</template>

<script>
import headerTab from './ui/commonUi/header.vue';
import toolbarInfo from './ui/commonUi/tooltip.vue';
    
export default{
    name: 'power_vue',
    components: {
        headerTab,
        toolbarInfo
        },
    data: () =>({
        voultage: '...', //  
        power: '...',    //   
        kpd: '...',      // 
        batteries: '...',//- 
        capacity: '...', // 
        result:0,        //,      
        self: false,     //,        
        started: false,  //,         ""
        rule: [
            value => !!value || '   .',
        ],                       //   
        snackbar: false,      //   
        timeout: 2500,
        errorText: '     ',


        //      
        componentInfo: {
            name: '      ',
            incomes: [
            '   ();', 
            '   (/);', 
            '-    (.);', 
            '  (0,8 - 1,0).',
            ],
            outcomes: [
            '       ;'], 
            description: `          .       ,        ,    (  ), , ,     .          . ,   ,              .`,
            disclaimers: [
            '      : ',
            't = U * C *  * h * Kp * Kg * (Q / P), :',
            't -       [];',
            "Ua -    [];",
            " -    [A*];",
            "K -      [];",
            "h -  ,         ;",
            "P      [],          ;",
            "    ;",
            
            ],
            constants: [
            {
                name: 'Kg-   (     ,       ,   25       1,75  ):', 
                massive: [ 
                    '   0,85'
                ]
            },
            {
                name: 'K-  :', 
                massive: [ 
                    '   0,85'
                ]
            },
            ]
        },

    }),
    methods:{
        start(){
            
            this.started = true
            if(this.self){ //   
                this.result =0.1 * Math.ceil(
                    10*this.voultage*this.capacity
                    *this.batteries*this.kpd
                    *0.85/this.power)

                console.log("  :   ", this.result, " ");
            }else{         //   
                this.power = ''
                this.power = localStorage.getItem('wats')
                this.result = 0.1 * Math.ceil(
                    10*this.voultage*this.capacity
                    *this.batteries*this.kpd
                    *0.85/this.power
                    )
                console.log("  :   ", this.result, " "); 
            }
            if (!this.result || this.result== Infinity){
                this.snackbar = true
                this.started = false
                return
            }
        },              //- 
        getPower(){
            return this.power = localStorage.getItem('wats')
        }               //-,        
      },
      mounted(){
          this.getPower()
      }
  }
</script>

<style>

</style>