<template>
    <div>
      <!--  -->
      <div class="header">
        <headerTab 
        title="  "
        v-bind:info = "componentInfo"
        ></headerTab>
      </div>        
        <v-card class="pa-5" tile>
            <!-- <h1> </h1> -->
            
            <!-- <v-checkbox 
                label="  ?" 
                v-model="WatOpt"
                class="mt-2"
            /> -->
            <v-switch
                v-model="WatOpt"
                :label="` : ${measureUnit}`"
            ></v-switch>


            <!--   -->
            <v-row class="mx-0 mb-2"> 

                <v-text-field
                :disabled="Usli_self == false "
                type="number"
                outlined
                required
                clearable
                label="   ()"
                :rules="rule"
                hide-details="auto"
                v-model.number="Usli_shd"
                class="mt-2"
                >
                    <template v-slot:label>
                        <toolbarInfo
                            title= "   ()"
                            :desc = componentInfo.incomes[0]
                        />
                    </template> 

                </v-text-field>

                <v-checkbox 
                    info
                    hide-details
                    label=" ?" 
                    v-model="Usli_self"
                    class="mt-5 ml-2"   
                />
            </v-row>
            

            <!--   -->
            <v-text-field
                type="number"
                outlined
                required
                clearable
                :rules="rule"
                hide-details="auto"
                v-model.number="Heat"
                class="mt-5"
            >
                <template v-slot:label>
                    <toolbarInfo
                        :title = heat_label
                        :desc = heat_label_description
                    />
                </template> 

            </v-text-field>
            
            <!--    -->
            <v-text-field
                type="number"
                outlined
                required
                clearable
                label="    (%)"
                :rules="rule"
                hide-details="auto"
                v-model.number="K_sred"
                class="mt-5"
                >
                <template v-slot:label>
                    <toolbarInfo
                        title = "    (%)"
                        :desc = componentInfo.incomes[1]
                    />
                </template> 

            </v-text-field>

            <v-text-field
                type="number"
                outlined
                required
                clearable
                :rules="rule"
                hide-details="auto"
                v-model.number="condition"
                class="mt-5"
            >
                <template v-slot:label>
                    <toolbarInfo
                        :title= condition_label
                        :desc = condition_label_description
                    />
                </template> 

            </v-text-field>

            <v-checkbox 
                label="  ?" 
                v-on:change="komutat_Opt"
                class="mt-5"
            />

            <div v-if="komutat_opt">
                <h2>  </h2>
                <!--  -->
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    label="  "
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="count_yadr"
                    class="mt-5"
                    >
                    <template v-slot:label>
                        <toolbarInfo
                            title= "  "
                            :desc = componentInfo.incomes[3]
                        />
                    </template> 

                </v-text-field>
                <!--  -->
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="komutat_yadr"
                    class="mt-5"
                >
                    <template v-slot:label>
                        <toolbarInfo
                            :title= komutat_yadr_label
                            :desc = komutat_yadr_label_description
                        />
                    </template> 
                </v-text-field>
                <!--  -->
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    label="  "
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="count_dost"
                    class="mt-5"
                >
                    <template v-slot:label>
                        <toolbarInfo
                            title= "  "
                            :desc = componentInfo.incomes[3]
                        />
                    </template> 

                </v-text-field>
                <!--  -->
                <v-text-field
                    type="number"
                    outlined
                    required
                    clearable
                    :rules="rule"
                    hide-details="auto"
                    v-model.number="komutat_dost"
                    class="mt-5"
                >
                    <template v-slot:label>
                        <toolbarInfo
                            :title= komutat_dost_label
                            :desc = komutat_dost_label_description
                        />
                    </template> 

                </v-text-field>
            </div>

            

            <v-btn 
                @click="start" 
                color="primary"
                class="mt-2"
            ></v-btn>

        </v-card>

        <!--    -->
        <v-snackbar
            v-model="snackbar"
            :timeout="timeout"
            
        >
        {{ errorText }}

            <template v-slot:action="{ attrs }">
                <v-btn
                color="accent"
                text
                v-bind="attrs"
                @click="snackbar = false"
                >
                
                </v-btn>
            </template>
        </v-snackbar>
    
    <!--   -->
    <v-card
        class="pa-5 mt-5"
        v-show="started"
    >
        <h2>  : </h2>
        <p><strong>{{ final_result }} .</strong></p>
        <v-btn
          @click="started=false"
          class="mt-2"
        ></v-btn>
    </v-card>

    



    </div>
</template>

<script>
import headerTab from './ui/commonUi/header.vue';
import toolbarInfo from './ui/commonUi/tooltip.vue';
export default {
    name: 'my-cooling',
    components: {
        headerTab,
        toolbarInfo
    },
    data:() =>({
        started: false,         //    (  )

        final_result:0,         //  

        komutat_opt: false,     //    
        Usli_self: false,       //       
        Usli_shd: '...',        //   


        snackbar: false,      //   
        timeout: 2500,
        errorText: '     ',


        K_sred: '...',          //     (0-100)

        WatOpt: true,           //     
        Heat: '...',            //   
        komutat_yadr:'...',     //   
        count_yadr:'...',       //   
        komutat_dost:'...',     //   
        count_dost:'...',       //   
        condition:'...',        //  

        rule: [
            value => !!value || '   .',
            ],                  //   
        
        //      
        componentInfo: {
            name: '  ',
            incomes: [
                    '    ,   .        ,   ;', 
                    '   ,     1,  100 (%).  ,           .      ;', 
                    '   1 . ', 
                    '    ,   .',
                ],
            outcomes: [
                    '   ,    . '
                ], 
            description: `                ..`,
            disclaimers: [
            "   -      ,       , -       ;",
            "    ;", 
            "  ,           ."           
            ]
        },

    }),
    methods:{
        start(){
            this.started = true
            let result = 0;
            if(this.komutat_opt==false){
                this.komutat_yadr = 0
                this.komutat_dost = 0
                this.count_yadr=0
                this.count_dost=0
                this.komutat_klast=0
            }

            console.log(this.WatOpt)
            if(this.WatOpt==false){

                result = (
                    (this.Heat*this.K_sred/100*this.Usli_shd)/3.41 
                    // +this.komutat_klast/3.41
                    +(this.count_yadr*this.komutat_yadr+this.count_dost*this.komutat_dost)/3.41
                    )
                // console.log('result', result)
                // console.log('Heat', this.Heat)
                // console.log('K_sred', this.K_sred)
                // console.log('Usli_shd', this.Usli_shd)
                // console.log('komutat_dost', this.komutat_dost)
                // console.log('komutat_opt', this.komutat_opt)
                // console.log('Condition', this.condition)
                this.final_result= Math.ceil(result/this.condition)
            }else{


                
                result = (this.Heat * (this.K_sred/100) * this.Usli_shd)

                // result += this.komutat_klast

                result += this.count_yadr * this.komutat_yadr + this.count_dost * this.komutat_dost


                // console.log('result', result)
                // console.log('Heat', this.Heat)
                // console.log('K_sred', this.K_sred)
                // console.log('Usli_shd', this.Usli_shd)
                // console.log('komutat_dost', this.komutat_dost)
                // console.log('komutat_opt', this.komutat_opt)
                // console.log('Condition', this.condition)
                this.final_result= Math.ceil(result/this.condition)
            }
            // console.log('AAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAAA', this.final_result)
            if (!this.final_result || this.final_result== Infinity){
                this.snackbar = true
                this.started = false
                return
            }
        },
        getusli(){
            this.Usli_shd = localStorage.getItem('usli')
            console.log(this.Usli_shd)
            return this.Usli_shd
        },
        komutat_Opt(){
            
            if(this.komutat_opt==false){
                this.komutat_opt = true
            }else{ this.komutat_opt = false}

            this.komutat_opt != this.komutat_opt
            if(this.komutat_opt==false){
                this.komutat_yadr = 0
                this.komutat_dost = 0
                this.count_yadr=0
                this.count_dost=0
            }else{
                this.komutat_yadr = '...'
                this.komutat_dost = '...'
                this.count_yadr='...'
                this.count_dost='...'
            } 
        },
    },
    mounted(){
        this.getusli()
    },
    computed: {
        measureUnit: function () {
            if (this.WatOpt) return ''
            else return 'BTU/hr'
        },

        condition_label: function () {
            return `  (${this.measureUnit})       `
        },

        condition_label_description: function () {
            return `,     .   ${this.measureUnit}       `
        },


        heat_label: function () {
            return `  (${this.measureUnit})       `
        },
        heat_label_description: function () {
            return this.componentInfo.incomes[2].toString() +`   ${this.measureUnit}       `
        },
        

        komutat_yadr_label: function () {
            return `   (${this.measureUnit})       `
        },
        komutat_yadr_label_description: function () {
            return `  .   ${this.measureUnit}       `
        },


        komutat_dost_label: function () {
            return `   (${this.measureUnit})       `
        },
        komutat_dost_label_description: function () {
            return `  .   ${this.measureUnit}       `
        },
    }
}
</script>

<style>

</style>