<template>
  <div
  >
    <!--  -->
    <div class="header">
      <headerTab 
        title="   "
        v-bind:info = "componentInfo"
      ></headerTab>
    </div>
    
    <!--   -->
    <v-card 
      class="pa-5 rounded-b"
      tile
      flat 
    >

      <v-text-field
        outlined
        type="number"
        required
        clearable
        label=" "
        :rules="rule"
        hide-details="auto"
        v-model.number="users"
        class="mt-5"
      >
        <template v-slot:label>
          <toolbarInfo
            title="  ()"
            desc = " ,       .    , "
          />
        </template> 
      </v-text-field>
      
      <v-text-field
        flat
        type="number"
        required
        outlined
        clearable
        label=" "
        :rules="rule"
        hide-details="auto"
        v-model.number="days"
        class="mt-5"
      >
        <template v-slot:label>
          <toolbarInfo
            title="  ()"
            desc = " ,         . ,  ,     "
          />
        </template> 
      
      </v-text-field>
      <!--    -->
      <v-checkbox
        info
        hide-details
        label="  ?"
        v-model="standart"
        class="mt-5"
      />
      <!--  -->
      <v-checkbox
        info
        hide-details
        label=" ?"
        v-model="converg"
        class="mt-5"
      />

      <!--    -->
      <template v-if="standart">
        <v-dialog
          v-model="dialog2"
          transition="dialog-bottom-transition"
          min-width="80%"
          :scrollable="false"
          aria-hidden="true"
        >
        <template v-slot:activator="{ props2 }">
            <v-btn
              class="mt-2 mt-5"
              width="100%"
              color="primary"
              v-bind="props2"
              @click="getStores"
              ><v-icon>mdi-cog</v-icon>
               
            </v-btn>
          </template>

          <div class="module_bg">
            <v-card
              class="pa-5"
            >

              <h1>   </h1>

              <v-text-field
                flat
                type="number"
                required
                outlined
                clearable
                label="  ()"
                :rules="rule"
                hide-details="auto"
                v-model.number="capacity"
                class="mt-5"
              >
                <template v-slot:label>
                  <toolbarInfo
                    title="  ()"
                    desc = " ,        ."
                  />
                </template> 
              </v-text-field>
              
              <v-text-field
                flat
                type="number"
                required
                outlined
                clearable
                label="  ()"
                :rules="rule"
                hide-details="auto"
                v-model.number="discs"
                class="mt-5"
              >
                <template v-slot:label>
                  <toolbarInfo
                    title="  ()"
                    desc = "    ,    ."
                  />
                </template> 
              </v-text-field>
              
              <v-btn
                class="mt-2 mt-5"
                width="100%"
                color="primary"
                @click="dialog2 = false"
                >
                  
              </v-btn>
            </v-card>
          </div>
          
        <!-- <div v-show="standart"> -->
          
        <!-- </div> -->
        </v-dialog>

      </template>

      <!--   -->
      <template v-if="converg">
        <v-dialog
          v-model="dialog1"
          transition="dialog-bottom-transition"
          width="80%"
          aria-hidden="true"
        >
          <template v-slot:activator="{ props1 }">
            <v-btn
              class="mt-2 mt-5"
              width="100%"
              color="primary"
              v-bind="props1"
              @click="getConverg"
              ><v-icon>mdi-cog</v-icon>
               
            </v-btn>
          </template>

          
          <!--    v-for -->
          <div class="module_bg">
            <v-card
              outlined
              class="pa-5"
            >
              
              <h1> </h1>
              

              <div 
                v-for="(server, index) in convServParam"
                :key="index"
                class="mt-5 inCardTab"
              >
                
                
                <v-text-field
                  v-model="server.title"
                  required
                  outlined
                  clearable
                  label=""
                  prepend-icon="mdi-table-column"
                  placeholder="hello"
                  :rules="rule"
                  hide-details="auto"
                  class="mt-2"
                >
                  <template v-slot:label>
                    <toolbarInfo
                      title=""
                      desc = "       ,    ."
                    />
                  </template>
                </v-text-field>
                
                <v-text-field
                  v-model="server.count"
                  type="number"
                  required
                  outlined
                  clearable
                  prepend-icon="mdi-counter"
                  label=" ()"
                  :rules="rule"
                  hide-details="auto"
                  class="mt-5"
                >
                  <template v-slot:label>
                    <toolbarInfo
                      title=" ()"
                      desc = "     ."
                    />
                  </template>
                </v-text-field>
                
                <v-text-field
                  v-model="server.volume"
                  type="number"
                  required
                  outlined
                  clearable
                  prepend-icon="mdi-disc-player"
                  label=" ()"
                  :rules="rule"
                  hide-details="auto"
                  class="mt-5"
                >
                  <template v-slot:label>
                    <toolbarInfo
                      title=" ()"
                      desc = ",     .   ."
                    />
                  </template> 
                </v-text-field>
                
                <v-btn
                  color="error"
                  @click="deleteItem(server, index)"
                  class="mt-5 mx-2 mb-2"
                >  
                  <v-icon>mdi-trash-can</v-icon>   
                </v-btn>

              </div>

              <v-btn
                color="primary"
                @click="extendConvServParamList"
                class="ma-5"
              ></v-btn>

              <!-- <v-btn
                color="success"
                @click="sumItUp"
                class="mt-5"
              > </v-btn> -->
          
            </v-card>
          </div>
        </v-dialog>
      </template>

      
      
      

      <!--   -->
      <v-select
        required
        v-model="options.value"
        density="comfortable"
        style="margin-top: 10px"
        hide-details
        outlined
        :items="options"
        item-text="name"
        item-value="value"
        label=" "
        class="mt-5"
      />

      <!--    -->
      <v-text-field
        ref="mBRRef"
        required
        type="number"
        style="margin-bottom: 20px"
        v-show="options.value == 'user'"
        flat
        outlined
        clearable
        label="/"
        :rules="rule"
        hide-details="auto"
        v-model.number="mBR"
        class="mt-5"
      >
        <template v-slot:label>
          <toolbarInfo
            title=" (/)"
            desc = "        .     ."
          />
        </template> 
      </v-text-field>


      <!--     -->
      <template v-if="options.value == 'video'">
        <v-dialog
          v-model="dialog"
          novalidate
          width="auto"
          aria-hidden="true"
          class="mt-5"
        >
          <template v-slot:activator="{ props }">
            <v-btn
              width="100%"
              color="primary"
              v-bind="props"
              @click="getVideo"
              class="mt-5 mr-5"
              ><v-icon>mdi-cog</v-icon>
               
            </v-btn>
          </template>

          <video1 @cam_bitrate="getMbrVideo" />

        </v-dialog>
      </template>



      <!--  -->
      <v-btn 
        @click="start" 
        color="primary" 
        class="mt-5"
        width="100%"
      ></v-btn>
    </v-card>


    <!--   -->
    <v-card 
      class="pa-5 mt-5"
      v-show="started"
    >
      <!-- <h2>     </h2> -->
        <p>     <strong>{{ volume }} TiB</strong> </p>
        <p>
                TiB   <strong>{{ volume1 }} </strong>
        </p>
        <div v-show="converg">
          <p>    <strong>{{ volume2 }} </strong></p>
          <p>        
            <strong>{{ volume3 }} ;</strong></p>
        </div>
        <p>   <strong>{{ usli }} ;</strong></p>
        <p>   <strong>{{ usli + 2 }} .</strong></p>

        <v-btn
          disabled
          class="mt-2  mr-5"
        > (WIP)</v-btn>
        <v-btn
          @click="started=false"
          class="mt-2"
        ></v-btn>
    </v-card>

    <!--    -->
    <v-snackbar
      v-model="snackbar"
      :timeout="timeout"
    >
      {{ errorText }}

      <template v-slot:action="{ attrs }">
        <v-btn
          color="accent"
          text
          v-bind="attrs"
          @click="snackbar = false"
        >
          OK
        </v-btn>
      </template>
    </v-snackbar>

  </div>

  


</template>

<script>
import video1 from "./video1.vue";
import headerTab from "./ui/commonUi/header.vue";
import toolbarInfo from './ui/commonUi/tooltip.vue';
export default {
  name: "shd_vue",
  components: {
    video1,
    headerTab,
    toolbarInfo
  },
  data() {
    return {
      snackbar: false,      //   
      timeout: 2500,
      errorText: '     ',


      //      
      componentInfo: {
        name: '   ()',
        incomes: [
          '     ;', 
          '  ()      ;', 
          '    ,           .',
        ],
        outcomes: [
          '       (.);',
          '     ( 2 );',
          '     ()   TiB ().'], 
        description: `           .     ,   ,    .     ,     .`,
        disclaimers: [
          '  - ,     ,       .          ,            ;',
          '    ,   ;',
          "     ,     ;",
          "     ."
        ],
        constants: [
          {
            name: '  :', 
            massive: [ 
                '  - 7   4 ',
                '   - 15   8 '
              ]
          },
          {
            name: '   :', 
            massive: [ 
                '      - 5 /; ',
                '  (ip-) - 0,1 /; ',
                '           - 8 /     ;',
                '  -   .'
              ]
          },
          {
            name: '    :', 
            massive: [ 
                '                     - 300 , 1 ; ',
                '            - 300 , 1 ; ',
                '                     - 600 , 3 ; ',
                '   - 300 , 1 ; ',
                '                         - 7680 , 3 ;',
              ]
          },
        ]
      },

      convServParam: [
        {id: 0, title: ` `, count: 1, volume: 300},
        {id: 1, title: `  `, count: 1, volume: 300},
        {id: 2, title: ` `, count: 3, volume: 600},
        {id: 3, title: `  `, count: 1, volume: 300},
        {id: 4, title: ` `, count: 3, volume: 7680}
      ], 
      //         
      sumItUpAnswer: 0, //    , 



      started: false,
      volume3: 0, //    
      volume2: 0, //   
      volume1: 0, //      TiB  
      volume: 0, //    
      mBR1: 0, //    (   )
      mBR: "...", //     (    V-model         )
      wats: "", //,        
      users: "...", //  
      usli: 0, //- 
      days: "...", //  
      capacity: "...", //    
      discs: "...", //-    
      standart: false, //,   / (   )
      converg: false, //,   / (  )
      dialog: false, //,    
      dialog1: false,
      dialog2: false,
      video: false, //,      
      rule: [(value) => !!value || "   ."], //   
      options: [
        { name: "  ", value: "local" },
        { name: " ", value: "phone" },
        { name: " ", value: "video" },
        { name: " ", value: "user" },
      ], // 

      convergChecked: false,
    };
  },
  methods: {
    
    //      
    extendConvServParamList(){
      // console.log('hello, making a new member')
      // console.log(this.convServParam)
      // console.log(this.convServParam.length)
      // console.log(this.convServParam[this.convServParam.length-1])
      this.convServParam.push({
        id: this.convServParam.length, 
        title: `type idk`, 
        count: 1, 
        volume: 0,
      }) 
      
      // console.log(this.convServParam)
    },
    deleteItem: function (item, index) {
      if(this.convServParam[index] === item) { 
      // The template passes index as the second parameter to avoid indexOf, 
      // it will be better for the performance especially for one large array
      // (because indexOf actually loop the array to do the match)
        this.convServParam.splice(index, 1)
        // console.log(this.convServParam)
      } else {
        let found = this.convServParam.indexOf(item)
        this.convServParam.splice(found, 1)
      }
    },
    
    sumItUp(){
      this.sumItUpAnswer = 0
      this.convServParam.forEach( servParam => {
        this.sumItUpAnswer += (servParam.count*servParam.volume)
      })
      console.log( `${this.sumItUpAnswer}       ${this.sumItUpAnswer/1024} `)
      return this.sumItUpAnswer
    }, //       
        
    getMbrVideo() {
      // console.log("cams_Mbr", (this.mBr = localStorage.getItem("Bitrate")));
      this.video = true;
      return (this.mBR = localStorage.getItem("Bitrate"));
    }, //-,      

    getVideo() {
      if (this.dialog === false) {
        this.dialog = true;
        this.convergChecked = true
      } else {
        this.dialog = false;
      }
    }, //-,     

    getConverg(){
        if (this.dialog1 === false) {
        this.dialog1 = true;
        this.convergChecked = true
      } else {
        this.dialog1 = false;
      }
    },

    getStores(){
        if (this.dialog2 === false) {
        this.dialog2 = true;
        this.standart = true
      } else {
        this.dialog2 = false;
      }
    },

    start() {
      this.started = true;
      this.getMbr();
      // console.log(this.mBR1);
      this.Volume();
      if(this.converg)
        this.Converg();

      this.Standart();
    }, //

    Power() {
      if (this.converg) {
        //   
        this.wats = (this.usli + 2) * 1000;
        // console.log(this.wats);
        localStorage.setItem("wats", null);
        localStorage.setItem("wats", this.wats);
        this.$emit("Power", this.wats);
      } else {
        //   
        this.wats = (this.usli + 2) * 700;
        // console.log(this.wats);
        localStorage.setItem("wats", null);
        localStorage.setItem("wats", this.wats);
        this.$emit("Power", this.wats);
      }
    }, //-,        

    Volume() {
      this.volume = this.mBR1 * +this.users * +this.days * 3600 * 24;
      // console.log("volume", this.volume);

      this.volume = Math.ceil(this.volume / 8000000);
      // console.log("   TiB:   ", this.volume);

      this.volume1 = Math.ceil(this.volume / 0.85 / 0.9095);
      // console.log(" ,   T:   ", this.volume1);
    }, //-,      

    Converg() {
        let server_volume = 0
        let disc_group = 0
        if(this.convergChecked){

        server_volume = this.sumItUp()/1024
        disc_group = Math.ceil(server_volume*2 / 0.85);
        this.volume2 = Math.ceil(disc_group+2*this.volume1)

        this.volume3= Math.ceil(2*this.volume1 + disc_group)/0.8
        }
        else{
            this.volume2= Math.ceil(this.volume1*2)
            // console.log('      ', this.volume2)
            this.volume3 = Math.ceil(this.volume2/0.7)
            // console.log('      ', this.volume3)
        }
    }, //-,      (  )

    getMbr() {
      this.mBR1 = 0;
      if (this.options.value == "local") {
        //  
        this.mBR1 = 5;
      }
      if (this.options.value == "phone") {
        // 
        this.mBR1 = 0.1;
      }
      if (this.options.value == "video") {
        // 
        if (this.video) {
          this.mBR1 = this.mBR;
        } else {
          this.mBR1 = 8;
        }
      }
      if (this.options.value == "user") {
        // 
        this.mBR1 += this.mBR;
      }
    }, //-,      

    Standart() {
      if (!this.standart){
        //  
        if (this.converg){
          //  
          this.usli = Math.ceil(this.volume3 / 7 / 4);
        } else {
          //  
          this.usli = Math.ceil(this.volume1 / 15 / 8);
        }
      } else{
        //  
        if (this.converg){ 
          //  
          this.usli = Math.ceil(this.volume3 / this.discs / this.capacity);
        } else {
          //   ,   ,     
          this.usli = Math.ceil(this.volume1 / this.discs / this.capacity);
        }

      }
      console.log(this.usli);
      console.log(!!this.usli);
      if (!this.usli){
        this.snackbar = true
        this.started = false
        return
      }
      localStorage.setItem("usli", this.usli);
      localStorage.setItem("usli", this.usli + 2);
      this.$emit("Usli", this.usli);
      this.Power()
      // console.log('emitting Usli')
      

    }, //-,         
  },
};
</script>
