<!-- 
 
     .
       ()


  title: String,    - ,       
  
  desc: String,     -  ,        

  imgRole:String,   -   ,    () 

  tipUrl: String,   -     

  srcUrl: String,   -        ( )

  imageUrl: String, -    ,   

-->
<template>
  <div>
      <v-tooltip
        right
        max-width="300px"
        min-width="40%"
        color="primary"
      >
        <template v-slot:activator="{ on }">
              <v-icon small v-on="on">mdi-help-circle-outline
              </v-icon>
              {{ title }} 
        </template>


        <!-- img by role & name -->
        <div v-if="tipUrl">
          <v-img
            :src="require(`${getImgUrl}`)" 
            aspect-ratio="1"
            gradient="to top right, rgba(100,115,201,.1), rgba(0,0,0,.1)"
          >
            <template v-slot:placeholder>
              <v-row
                class="fill-height ma-0"
                align="center"
                justify="center"
              >
                <v-progress-circular
                  indeterminate
                  color="grey lighten-5"
                ></v-progress-circular>
              </v-row>
            </template>
          </v-img>
        </div>

        <!-- img by raw src -->
        <div v-if="srcUrl">
            <v-img
              :src="require(`${srcUrl}`)" 
              aspect-ratio="1"
              gradient="to top right, rgba(100,115,201,.1), rgba(0,0,0,.1)"
            >
              <template v-slot:placeholder>
                <v-row
                  class="fill-height ma-0"
                  align="center"
                  justify="center"
                >
                  <v-progress-circular
                    indeterminate
                    color="grey lighten-5"
                  ></v-progress-circular>
                </v-row>
              </template>
            </v-img>
          </div>




        <!--   -->
        <div>
   
          <!--   -->
          <v-img v-if="imageUrl=='autumnBg'" src="./images/theme/autumnBg.jpg" />
          
          <v-img v-if="imageUrl=='forestBg'" src="./images/theme/forestBg.jpg"/>

          <v-img v-if="imageUrl=='sunsetForestBg'" src="./images/theme/sunsetForestBg.webp" />

          
          <!--    -->

          <v-img 
            v-if="imageUrl=='verticalFOV' "  
            src="./images/cameraTips/__.png" 
          />

          <v-img 
            v-if="imageUrl=='horizontalFOV'" 
            src="./images/cameraTips/__.png" 
          />

          <v-img 
            v-if="imageUrl=='placementHeight'" 
            src="./images/cameraTips/__.png" 
          />

          <v-img 
            v-if="imageUrl=='blindZone'" 
            src="./images/cameraTips/__.png" 
          />
          
          <v-img 
            v-if="imageUrl=='PPMZones'" 
            src="./images/cameraTips/_PPM.png" 
          />

          <v-img 
            v-if="imageUrl=='cameraTilt'" 
            src="./images/cameraTips/__.png" 
          />

          <v-img 
            v-if="imageUrl=='viewDistance'" 
            src="./images/cameraTips/___.png" 
          />


          <!-- VLS tips -->


          <v-img 
            v-if="imageUrl=='strelaProvesa'" 
            src="./images/vlsTips/strelaProvesa.jpg" 
          />

          <v-img 
            v-if="imageUrl=='perepadVisot'" 
            src="./images/vlsTips/perepadVisot.jpg" 
          />
          

        </div>



        <!--    -->
        <p class="text-center" v-if="imageUrl==''"> </p>
          
        <!--  -->
        <p class="text-center" v-if="desc" > {{ desc }}</p>
        

      </v-tooltip>
    </div>

</template>

<script>
// import a from '@/components/ui/commonUi/images/vlsTips/'

export default {
    name: "toolbarInfo",
    data() {
        return {          
          defaultImage: 'autumnBg',
          imageRoles: [

          ],
        };
    },
    props: {
        title: String,

        imageUrl: String, //    ,   

        imgRole:String,

        tipUrl: String,

        srcUrl: String,

        desc: String,
    },
    computed: {
      getImgRole (){
        if (this.imgRole == 'vlsTips' ) return './images/vlsTips/'
        //    
        else if (this.imgRole == 'cameraTips' ) return './images/cameraTips/'
        //    
        return './images/theme/'
        // if no role, then default
      },
      getImgUrl () {
        return `${this.getImgRole}${this.tipUrl}`
      }
    }
};
</script>
  
  